export default {
    
    props: ['domainStats'],
    
    data() {
        return {
            domain_name: "",
            appraisal_type: "fast",
            email: "",

            submitSuccess: false,
            submitError: false,
            submitText: 'Saved!',
            suggest: false,
        }
    },
    methods: {
        async submitForm() {
            await this.$axios.$post('/open/send/appraisal', {
                'name': this.domain_name,
                'email_buyer': this.email,
                'appraisal_type': this.appraisal_type,
            })
            .then((response) => {
                // console.log(response)
                this.domain_name = ""
                this.email = ""
                this.appraisal_type = ""

                this.submitSuccess = true;
                this.submitText = 'Saved!';
                setTimeout(() => this.submitSuccess = false, 4000)

                this.$router.push('/domain/appraisal/add/success');
            }, (error) => {
                // console.log(error.response.data.message)
                this.submitError = true;
                this.submitText = error.response.data.message;
                setTimeout(() => {
                    this.submitError = false, 
                    this.submitText = ''
                }, 4000)
            });
        }
    },
}
